@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600&display=swap);
*{margin:0;padding:0;box-sizing:border-box}body{font-family:sans-serif;background-color:#0d1821;display:flex;flex-direction:column;justify-content:center;align-items:center;min-height:100vh;text-align:center;color:white;font-family:poppins}.rules{height:100vh;width:25%;position:absolute;top:0;left:-25%;background-color:rgba(255,255,255,0.13);transition:0.3s;display:flex}.rules.active{left:0}footer{position:absolute;bottom:0;left:0;width:100%}


.board{width:500px;height:500px;display:grid;grid-template-columns:repeat(3, 1fr);grid-template-rows:repeat(3, 1fr);grid-gap:10px}.board .square{background-color:#2e4052;border:1px solid rgba(255,255,255,0.05);border-radius:1.5rem;display:flex;justify-content:center;align-items:center;color:white;font-weight:600;font-size:5rem;cursor:pointer;transition:0.2s}.board .square:hover{background-color:#253342}.reset-btn,.rules-btn{padding:10px;background-color:transparent;font-size:1rem;text-transform:uppercase;font-weight:600;border:none;margin:3px;position:relative;cursor:pointer;overflow:hidden;color:#8e94f2}.reset-btn::after,.rules-btn::after{position:absolute;content:"";bottom:0;left:-100%;width:100%;height:3px;background-color:#8e94f2;transition:0.3s}.reset-btn:hover::after,.rules-btn:hover::after{left:0}.counter{margin:5px;padding:10px}.score{display:flex;justify-content:center}

